import * as React from 'react';
import { useState } from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ThumbUpAltRoundedIcon from '@mui/icons-material/ThumbUpAltRounded';
import AddCommentRoundedIcon from '@mui/icons-material/AddCommentRounded';
import IconButton from '@mui/material/IconButton';
import Grid from '@mui/material/Grid';
import { Divider, Typography } from '@mui/material';
import Dialog from'@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import FeedDetail from './FeedDetail';
import MoreMenu from './MoreMenu';

//   5 ,  ... 
const Content = styled(Typography)`
	overflow: hidden;	
	display: -webkit-box;
	-webkit-line-clamp: 5;
	-webkit-box-orient: vertical;
`

export default function Feed(props) {

	const comments = [
		{
			"comment_idx" : 1,
			"name" : "1",
			"image" : "https://placeimg.com/100/100/people/1",
			"content" : "      .      . ,    .    .      .      . ,    .    .      .      . ,    .    ."
		},
		{
			"comment_idx" : 2,
			"name" : "2",
			"image" : "https://placeimg.com/100/100/people/2",
			"content" : "    ,    , .      .    .  ,      .         ."
		}
	];
	const [open, setOpen] = useState(false);
	const openContent = () => { setOpen(true); };
	const closeContent = () => { setOpen(false); };
	const [anchor, setAnchor] = useState(null);
	const handleClickProfile = (e) => { setAnchor(e.currentTarget); };
	const handleCloseProfile = () => { setAnchor(null); };
	const handleClickProfileView = (e) => { 
		props.getUser([props.name, props.image]);
		props.getMode("PROFILE");
	}

	return (
		<>
			<Paper sx={{ margin: '14px' }}>
				<Grid container>
					<Grid item xs={11} sx={{cursor: 'pointer'}}
						onClick={openContent}> {/*  */}
						<Box p={4}>
							<Content>{props.content}</Content>
						</Box>
					</Grid>
					<Grid item xs={1} p={2}> {/*   */}
						<MoreMenu/>
					</Grid>
				</Grid>
				<Divider/>
				<Grid container p={2}>
					<Grid item xs={10}> {/* ,  */}
						<Box>
							<IconButton>
								<ThumbUpAltRoundedIcon sx={{ fontSize: 30 }}/>
							</IconButton>
							<IconButton>
								<AddCommentRoundedIcon sx={{ fontSize: 30 }}/>
							</IconButton>
						</Box>
					</Grid>
					<Grid item xs={2}> {/*  */}
						<Box>
							<Grid container spacing={1} sx={{cursor: 'pointer'}} 
								onClick={handleClickProfile}>
								<Grid item>
									<Avatar alt="profile image" src={props.image}/>
								</Grid>
								<Grid item>
									<Typography>{props.name}</Typography>
									<Typography variant="body2" color="textSecondary">{props.time}</Typography>
								</Grid>
							</Grid>
							<Menu anchorEl={anchor} open={Boolean(anchor)} onClose={handleCloseProfile}
								anchorOrigin={{
									vertical: 'bottom',
									horizontal: 'center',
								}}
							>
								<MenuItem onClick={handleClickProfileView}></MenuItem>
								<MenuItem></MenuItem>
								<MenuItem></MenuItem>
							</Menu>
						</Box>
					</Grid>
				</Grid>
			</Paper>

			{/*    */}
			<Dialog open={open} onClose={closeContent} fullWidth={true} maxWidth='md'>
				<DialogContent>
					<FeedDetail
						userName={props.userName}
						userImg={props.userImg}
						name={props.name}
						content={props.content}
						image={props.image}
						time={props.time}
						comments={comments}
						closeContent={closeContent}
					/>
				</DialogContent>
			</Dialog>
		</>
	);
}